<template>
	<article class="article transport">
		<h2 class="header form--header header__no-underline">Информация о транспортном средстве</h2>
		<div class="transport--wrap">
			<fieldset class="form--block">
				<legend class="sub-header sub-header__active">Тип транспортного средства</legend>
				<div class="form--row">
					<template v-for="(el, index, key) in trans">
						<input :id="'trans-type'+index"
							   type="radio"
							   :value="el.type"
							   @change="onTransTypeChange($event.target.value, index)"
							   :checked="index == selectedTransIndex"
							   name="trans-type"
							   class="form--radio">
						<label class="form--label form--label__transport" :class="'form--label__' + el.class" :for="'trans-type'+index">
						<div class="label--wrap">
							<div class="form--icon-wrap">
								<svg class="form--icon" :class="'form--icon__' + el.class" aria-hidden="true" :viewBox="el.viewBox">
									<use v-bind="{'xlink:href':'Content/img/icons.svg#'+el.class}"></use>
								</svg>
							<p class="label--text">{{el.type}}</p>
							</div>
						</div>
						</label>
					</template>
				</div>
			</fieldset>

			<fieldset class="form--block" >
				<legend class="sub-header">{{trans[selectedTransIndex].extra.name}}</legend>
				<div class="form--row">
					<div class="transport--el" v-for="(param, index, key) in trans[selectedTransIndex].extra.params">
						<input :id="'trans-extra'+index"
							   type="radio"
							   :key="index"
							   :value="param"
							   :checked="index == selectedExtraIndex"
							   @change="changeTransExtra($event.target.value)"
							   name="trans-extra"
							   class="form--radio">

						<label class="form--label form--label__extra" :key="index + '0'" :for="'trans-extra'+index">{{param}}</label>
					</div>
				</div>
			</fieldset>
		</div>
	</article>
</template>

<script>
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";

	export default {
		data() {
			return {
				trans: [
				{
					type: "Легковой",
					class: "passenger-car",
					viewBox: "0 0 512 237",
					extra: {
						name: "Объем двигателя",
						params: ["до 1600 см", "1601 - 2000 см", "2001 - 3000 см", "3001 см и более"]
					}
				},
				{
					type: "Электромобиль",
					class: "electric-car",
					viewBox: "0 0 337 328",
					extra: {
						name: "Мощность",
						params: ["до 60,7 кВт", "60,8 - 76 кВт", "76 - 114 кВт", "114 кВт и более"]
					}
				},
				{
					type: "Автобус",
					class: "bus",
					viewBox: "0 0 582 327",
					extra: {
						name: "Количество мест",
						params: ["до 20 включительно", "21 и более"]
					}
				},
				{
					type: "Грузовик",
					class: "truck",
					viewBox: "0 0 612 434",
					extra: {
						name: "Масса автомобиля",
						params: ["до 2 тонн включительно", "2 тонны и более"]
					}
				},
				{
					type: "Мотоцикл",
					class: "moto",
					viewBox: "0 0 337 240",
					extra: {
						name: "Объем двигателя",
						params: ["до 300 см", "300 см и более"]
					}
				},
				{
					type: "Прицеп",
					class: "trailer",
					viewBox: "0 0 492 289",
					extra: {
						name: "Тип прицепа",
						params: ["к легковому авто", "к грузовому авто"]
					}
				}
				]
			}
		},
		computed: {
			...mapGetters("transport",{
				transType: "transType",
				transExtra: "transExtra"
			}),
			selectedTransIndex() {
				for (let i = 0; i < this.trans.length; i++) {
					if(this.trans[i].type === this.transType) {
						return i
					}
				}
			},
			selectedExtraIndex() {
				const extra = this.trans[this.selectedTransIndex].extra.params;
				for (let i = 0; i < extra.length; i++) {
					if(extra[i] === this.transExtra) {
						return i
					}
				}
			},
		},
		methods: {
			...mapActions("transport",{
				changeTransType: "changeTransType",
				changeTransExtraName: "changeTransExtraName",
				changeTransExtra: "changeTransExtra"
			}),
			onTransTypeChange(value, index) {
				this.changeTransType(value);
				this.changeTransExtraName(this.trans[this.selectedTransIndex].extra.name);
				const firstExtra = this.trans[this.selectedTransIndex].extra.params[0];
				this.changeTransExtra(firstExtra);
			}
		},
		updated() {
      console.log(localStorage);
    }
	}
</script>

<style lang="less">
.transport {
	&--block {
		margin-right: 50px;
	}
	&--el {
		display: inline-flex;
		@media only screen and (max-width:320px) {
			display: flex;
			width: 100%;
			justify-content: center;
		}
	}
}

.form {
	&--label {
		&__transport {
			padding: 10px 0;
			@media only screen and (max-width:1195px) {
				height: 111px;
			}
			@media only screen and (max-width:477px) {
				height: 123px;
			}
			@media only screen and (max-width:477px) {
				height: 123px;
			}
			@media only screen and (max-width:400px) {
				height: 113px;
			}
			@media only screen and (max-width:374px) {
				height: 109px;
			}
			@media only screen and (max-width:320px) {
				height: 100px;
			}
		}
		&__extra {
			align-items: center;
			@media only screen and (max-width:576px) {
				max-width: none;
			}
		}
	}
	&--icon {
		&__electric-car {
			transform: scale(-1, 1);
		}
		&__truck {
			transform: scale(-1, 1);
		}
		&__moto {
			transform: scale(-1, 1);
		}
	}
}
</style>




